.cart{
    width: 100%;
    height: 50vh;
    display: flex;
    justify-content: space-between;
    background-color: rgb(223, 217, 191);
}
.cart-details{
             padding: 3rem 4rem;

}
.cart-details img{
    width:400px;
    object-fit: cover;
}
.cart-details h2{
    text-align: start;
    color: coral;
    font-family: 'Abril Fatface', cursive;
    font-weight: 900;
    font-size: 3rem;
    margin: 10px 0;
}
.cart-details h2 span {
        color: darkgray;
        font-weight: 900;
        letter-spacing: 1.5px;
        margin: 10px 0;
        text-align: start;
        font-size: small;
}
.cart-details p{
          color: rgb(59, 57, 57);
          font-family: Verdana, Geneva, Tahoma, sans-serif;
          font-weight: 500;
          text-align: justify;
}
@media(max-width:850px){
    .cart{
        display: block;
        width: 100%;
        height: 100vh;
    }
}