.services{
    background-image: url('./Gallery/g.jpg');
    background-position: center;
    background-repeat: no-repeat;
    width: 100%;
    min-height: 50vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.fac-itms{
    margin: 4rem 2rem;
 
}
.fac-list{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 40px 0;
    flex-wrap: wrap;
}
.fac-menu img{
             height: 100px;
}
.fac-menu p{
    color: chocolate;
    margin: 20px 0;
    font-weight: 800;
}

