.places{
    width: 500px;
   
}
.near{
   display: grid;
   grid-template-columns:  1fr 1fr 1fr ;
   margin: 4rem -2rem;
   padding: 0 20rem 0 15rem;
}
.places h4{
    text-align: center;
    color: darkgoldenrod;
    font-weight: 700;
    font-size: 3rem;
}
.marquee{
    margin: 4rem ;
}
.mar img{
    height: 100px;
}
.quue{
    display: flex;
    align-items: center;
    justify-content: space-between;
}





