.home{
    background-image: url('../Images/b.jpeg');
    width: 100%;
    min-height: 60vh;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;
}
header h1{
    color:aliceblue;
    font-family: 'Great Vibes', cursive;
    font-weight: 800;
    animation-delay: 1s;
   
}
header p{
    color: aliceblue;
    font-family: 'Poppins', sans-serif;
    width: 50%;
    font-style: oblique;
    font-weight: 700;
    animation-delay: 1.5s;
}
/* section */
section{
       margin: 4rem 24rem;
    position: absolute;
    top:250px;
}
form{
    background-color: rgb(217, 223, 138);
    padding: 4rem 2rem;
    display: flex;
    justify-content: space-between;
    column-gap: 10px;
}
.form-details label{
        color: rgb(26, 32, 32);
        font-size: small;
        text-align: start;
        font-weight: 2rem;
}
.form-details input{
     padding: 5px 10px;
     text-align: center;
     margin: 0 5px;
}
 .select{
    padding: 5px 10px;
}
.option{
    padding: 2px 3px ;
}
.form-details button{
                 background-color: chocolate;
                 color: aliceblue;
                 border: none;
                 padding: 5px 10px;
                 white-space: nowrap;
                margin-top: 30px;
                font-weight: 600;
}

/* time */
.time{
    margin: 6rem 4rem;
   
}
.time-details{
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 10px;
    width: 100%;
}
.clock h2{
    margin: 20px 0;
    font-size: 1.3rem;
    color: rgb(51, 49, 49);
    font-weight: 400;
    text-decoration: underline;
    text-decoration-color: darkgoldenrod;
    text-underline-offset: 10px;
}
.clock p{
    color: rgb(9, 8, 8);
    letter-spacing: 2px;
    margin: 15px 0;
}
.anim{
    opacity: 1;
    transform: translateX(30px);
    animation: moveup 0.5s forwards;
  
}
.land{
    margin: 10rem auto;

}
@keyframes moveup{
    100%{
        opacity: 1;
        transform: translateX(0px);
    }
}






















@media(max-width:850px){
       section{
        margin: 8rem 10rem ;
        width: 440px ;

        
       }
        form{
            display: grid;
            grid-template-columns:  1fr 1fr ;
            row-gap: 5px;
        }
        .form-details label {
                         display: block;
                         font-weight: 800;
        }
        .form-details input{
            width: 155px;

        }
        .time{
            margin-top: 450px;
        }
       .time-details{
        display: block;
       }
}