.flip{
    margin:4rem  ;
}
.flip h1{
    color: darkgoldenrod;
    font-size: x-large;
    font-weight: 700;
}
.flip-cart{
   display: flex;
   justify-content: space-between;
   gap: 20px;
   margin: 4rem 6rem;
}
.flip-cart-front{
    width:350px;
  height: 60vh;
  border: 1px solid rgb(146, 179, 38);
}
.flip-cart-front img{
    width: 100%;
    object-fit: cover;
}
.flip-back h5{
    margin: 5px 0;
  text-align:start;
    color: black;
    font-weight: 800;
}
.flip-inner{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.flip-inner h6{
    font-size: small;
    margin: 0 6px;
}
.flip-fam p{
    text-align: start;
    margin: 20px 15px ;
    color: black;
    font-weight: 800;
}
.flip-btn button{
    background-color: darkgoldenrod;
    color: aliceblue;
    border: none;
    padding: 10px 15px;
    font-weight: 900;

}
@media(max-width:850px){
    .flip-cart{
        display:block;
          grid-column: 10px;
        margin: 4rem 12rem;
     }
}