.navbarItems{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 30px;
    box-shadow: 0 5px 15px 0 rgba(0,0,0,.25);
    width: 100%;
    height: 80px;
    background-color: rgb(44, 40, 40);
    position: fixed;
    z-index: 99;
}
.nav-logo span{
           color: chocolate;
           font-size: 1.9rem;
           padding: 0 3px;
           cursor: pointer;
}
.nav-logo{
              color: aliceblue;
              letter-spacing: 1px;
              font-size: 1.3rem;
              align-items: center ;
}
.nav-menu{
    display: grid;
    grid-template-columns: repeat(6,auto);
    gap: 10px;
    list-style: none;
    align-items: center;
}
.nav-links{
            text-decoration: none;
            color: aliceblue;
            padding: 0 0.7rem;
            white-space: nowrap;
           font-weight: 700;
}
.nav-links i{
    padding: 0 4px;
}
.nav-links:hover{
    color: aliceblue;
    background-color: rgb(55, 189, 144);
    text-decoration: none;
    padding: 10px 15px;
    border-radius: 10px;
    transition: 0.7s ease-in ease-in-out;
}
.menu-icons{
    display:none;
}
@media(max-width:850px){
    .navbarItems{
        z-index: 99;
    }
    .nav-menu{
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        background: rgb(194, 110, 15);
       
        width: 100%;
        height: auto;
        position: absolute;
        top: 0;
        left: -100%;
        opacity: 1;
        align-items: stretch;
        padding: 80px 0 30px 0; 
        margin: 0;
        z-index:1000;
    }
    .nav-menu.active{
        left: 0;
        opacity: 1;
        z-index:-1;
    }
    .nav-links{
        display: block;
        width: 100%;
        font-size: 1.2rem;
        padding: 2rem 0;
        color: rgb(242, 239, 239);
    }
    .nav-links:hover{
        transition: 0.9s;
    }
    .menu-icons{
        display: block;
       
    }
    .menu-icons i{
        color: aliceblue;
        font-size: 1.5rem;
        font-weight: 700;
    }
}