.food{
    background-image: url('../Images//f.jpg');  
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    min-height: 50vh;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
}
.food-menu{
    margin: 4rem 2rem;
}
.food-menu h2{
    color: chocolate;
    margin-bottom: 20px;
}
.menu-items{
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
  
}
.food-items{
    width: 200px;
}
.food-items img{
    width:100%;
    height: 150px;
    border: 1px solid orangered;
    object-fit: cover;
    white-space: nowrap;
}
.food-items p{
      font-weight: 700;
}
/* food review */

.food-review{
    margin: 5rem 0;
    display: flex;
    flex-direction: column;
    background-color:whitesmoke;
    width: 100%;
}
 h5{
       display: flex;
               padding: 10px 25px;
              font-size: larger;
              font-weight: 800;
              cursor: auto;
              color: chocolate;
}
.food-photos{
    display:grid;
    grid-template-columns: 1fr 1fr ;
    width: 100%;
    margin: 5rem 2rem;
}
.food-photos img{
    width: 350px;
    height: 150px;
    padding-left: 5rem;
}
.food-photos p{
    text-align: justify;
    padding: 0 15px;
    width: 450px;
    font-size: larger;
}