footer{
    width:100%;
    background-color: black;
    color: aliceblue;
}
.footer{
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 8rem 12rem;
}
address h2{
    font-size:1.4rem;
    font-weight: 500;
    background-color: chocolate;
    padding: 5px 10px;
}
address p{
    margin: 10px 0;
}
.contact h2{
    font-size:1.4rem;
    font-weight: 500;
    background-color: chocolate;
    padding: 5px 10px;
    margin-bottom: 10px;
}
.contact i{
    color: aliceblue;
    margin-bottom: 10px;
    padding-left: 5px;
}
.contact a{
    text-decoration: none;
    font-size: medium;
    margin: 0 5px;
}
@media(max-width:850px){
    .footer{
        display: flex;
        align-items: center;
        flex-direction: column;
  }    
}