header.bed{
    background-image:url('../Services/Gallery/r1.jpg');
    width: 100%;
    min-height: 50vh;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.room{
    margin: 4rem 2rem;
}
 h1{
    color: coral;
    font-weight: 900;
    font-size: 3rem;
    font-family: 'Lobster', cursive;
}
.room span{
    font-size: small;
    color: darkgray;
    letter-spacing: 2px;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-weight: 800;
}
.more{
    display: flex;
    align-items: center;
    justify-content: space-between;
    row-gap: 15px;
}
.bed-details{
    width: 450px;
    border: 1px solid  rgb(169, 114, 97);
    padding: 2rem 4rem;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}
.bed-details img{
    width:100%;
    object-fit: cover;
}
.bed-details h4{
    margin: 15px 0;
    color: black;
    font-weight: 800;
}
.bed-details h5{
       font-size: medium;
       padding: 0px 5px;
}
.bed-details h5 span{
    padding: 0px 5px;
    color: brown;
    font-weight: 600;
}
.click{
    background-color: chocolate;
    color: aliceblue;
    border: none;
    padding: 5px 30px;
    margin: 10px 0;
    font-weight: 600;
}
.reviews{
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: rgb(82, 158, 69);
    padding: 4rem 10rem;
    min-height: 70vh;
    row-gap: 15px;
}
.review h2{
    color: rgb(37, 188, 138);
    margin: 20px 0;
    border: 1px solid white;
    background-color: aliceblue;
    padding: 25px 55px;
    font-size: 5rem;
    border-radius: 80%;
}
.review small{
    color: aliceblue;
    font-weight: 800;
    font-size: 2rem;
}

.div img{
    width: 250px;
    border-radius: 45%;
    object-fit: cover;
}

.say h1{
    color: aliceblue;
    margin-bottom: 20px;

}
.say p{
    width: 280px;
    color: aliceblue;
    margin-bottom: 20px;
   text-align: center;
}















@media(max-width:850px){
         .more{
            display: block;
            padding: 0 5rem;
            margin-bottom: 10px;
            row-gap: 15px;
         }
         .reviews{
            display: block;
        
         }
         .review h2{
            border: 1px solid white;
            padding: 10px;
            width: 150px;
            border-radius: 10px;
            margin: 0 12rem 20px;
         }
         .say p{
            width: 100%;
            color: aliceblue;
            margin-bottom: 20px;
            text-align: justify;
        }
}