.mobile{
    background-image: url('./Images/c.jpeg');
    width: 100%;
    min-height: 60vh;
    background-position: center;
    background-repeat:no-repeat;
    background-size: cover;
    display: flex;
    align-items: center;
    justify-content: center;
}
.cont{
    margin: 4rem 2rem;
    display: flex;
    justify-content: space-between;
}
.call{
    margin-left: 4rem;
}
.call form{
    background-color: aliceblue;
      display: block;
}
.contact-details label{
           
            display: block;
            margin-bottom: 10px;
            font-size:1rem ;
            font-weight: 700;
}
.contact-details input{
    width: 400px;
    line-height: 40px;
    margin-bottom: 10px;
}
.contact-details textarea{
    width: 400px;
    line-height: 30px;
}
.contact-details button{
       padding: 5px 25px;
       margin: 20px 0;
       background-color: coral;
       color: aliceblue;
       border: none;
}
iframe{
    width: 600px;
    height: 480px;
    border: 0;
    margin-right:40px;
}
.frame p{
    color: coral;
    font-weight: bold;
    font-size: 1.6rem;
    margin: 20px 0;
}
@media(max-width:850px){
    .cont{
        margin: 4rem 2rem;
        display: block;
    }
}
.error{
    color: red;
    font-size: 1rem;
    margin: 10px 0;
}